.request {
	background: @m_c;
    padding-top: 125px;

	   .center{

	max-width: 830px;
    padding: 0px 15px;
    margin: 0px auto;
    }
}

.request_title {
	   margin-top: 25%;
    max-width: 800px;
    margin: 0 auto;
    padding-bottom: 40px;
    &:before, &:after {
    	content: none;
    }

}

.request_title {
    .circle-line, .circle-round {
        background: #161616 ;
    }

    
}

.text-field {
	border: 1px solid #ffffff;
    background: 0 0;
    margin-bottom: 20px;

    font-size: 16px;
    color: #fff;
    padding: 15px 0 15px 25px;
    text-align: left;
    width: 100%;
    max-width: 568px;
    resize: none;
    outline: none;
    transition: all 0.4s ease;
   

    &::-webkit-input-placeholder {
    	color: #fff;
    }

     &::-moz-input-placeholder {
    	color: #fff;
    }
}


.text-field:active, .text-field:focus{
	border: 1px solid #000000;
	
}

.request-form {
	width: 100%;
}

.coach_specialization-cardImg img, .coach_specialization-cardImg{
    width: 130px;
    height: 130px;
}

.request-subtitle {
    padding-bottom: 40px;
    letter-spacing: .05em;
   
    font-size: 20px;
}

button.second-btn {
    background: #161616;
    margin-top: 8px;
 }


 input.text-field {
   font-family: 'Open Sans', sans-serif;
 }
