.coachs{

 	background: url(../img/texture.png) center/cover no-repeat , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
    color: #fff;
   padding-top: 125px;
   
  

    .cover-box {
    	position: absolute;
    	background: url(../img/texture.png) center/cover no-repeat , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
    }

    .center{

	max-width: 1200px;
    padding: 0px 15px;
    margin: 0px auto;
    }

    .flex-container {
        align-items: stretch;
    }

     .coach_info-descr {
    margin-top: 3px;
 }

}

.coach-title {
       margin-top: 25%;
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 67px;

}



.next, .next-2, .next-3 {
    position: absolute;
    top: 50%;
    right: -5%;
    height: 16px;
    overflow: hidden;
    z-index: 8;
    padding-bottom: 2px;
        svg {
        fill: #ffffff;

        transform: translateY(-2px);

    }
}

.prev, .prev-2, .prev-3 {
    position: absolute;
    top: 50%;
    left: -5%;
    z-index: 8;
      overflow: hidden;
     height: 16px;
    transform: rotate(180deg);
        svg {
        fill: #ffffff;
        transform: translateY(-2px);
    }
}


.coach_photo_wrap {
    position: relative;
    flex: 0 1 42%;
    &:before {

    }
}


.coach_descr .coach_info-text {
    line-height: 1.55;
    font-size: 1.5vw;
    margin-top: auto;
    margin-bottom: 20px;
    letter-spacing: 0.7px;
}

 .coach_info-descr {
    font-size: 1.66vw;
 }
.coach_descr {
    display: flex;
    flex-direction: column;
    flex: 0 1 50%;
    & > * {
        margin: 0;
        text-align: left;
    }
}

.coach-photo {
    position: relative;
    z-index: 2;
    border: 8px solid #fff;
    & > * {
        position: relative;
        z-index: 3;
    }

}


.coach_photo-before {
    position: absolute;
    width:  100%;
    height:     100%;
    background: @m_c;
    top: 12%;
    left: -8%;
}

.all_coaches {
    flex-direction: column;
    margin-top: 2%;
    align-items: flex-start;
}


.coach_specialization-infoWrap {
    display: flex;  
    justify-content: space-between;
    align-items: stretch;
    position: relative;
    margin:  0 -25px;
}    

.coach_specialization:nth-child(3) > .coach_specialization-infoWrap,
.coach_specialization:nth-child(2) > .coach_specialization-infoWrap {
    margin:  0 0px;
}   

.coach_specialization-title {
    text-align: left;
}
.coach_specialization {
    width:  100%;
}


.coach_specialization:last-child {
    .coach_specialization-infoWrap {
        justify-content: center;
        .coach_specialization-card {
            width: 28%;
        }
    }
}

.coach_specialization-card {
    .flex();
    height: auto;
    display: flex !important;
    justify-content: flex-start;
    align-items: stretch;
    width:  33.33%;
    margin-right:  25px;
    margin-left:    25px;
    position: relative;
    z-index: 50;

}

.coach_specialization-slider {
    width:  100%;
    .slick-list {
    margin: 0 -25px;

    }

    .coach_specialization-card {
   
        margin-left: 25px;
        margin-right: 25px;
    }
}

.coach_specialization-cardName {
    text-align: left;
        max-width: 80px;
        
}

.coach_specialization-cardInfo {
    margin-left: 15px;
    display: flex;
        justify-content: space-between;
        flex-direction: column;
        flex: 0 1 60%;
}

.coach_info-title {
    margin-top: 20px;
}

.coach_specialization-cardDescr {
    font-size: 1.5vw;
}

.coach_specialization-cardImg img, .coach_specialization-cardImg{
    width: 130px;
    height: 130px;
    flex: 0 1 130px;
}

.coach_specialization-cardImg img {
    max-width: 130px;
}

.all_coaches .coach_specialization .coach_specialization-infoWrap {
    margin-top: 4.6%;
}

.coach_specialization {
    margin-top: 6.45%;
}

.coach_specialization:first-child {
    .coach_specialization-card {
        max-width: 356.67px;
    }
}

.quotes {
    position: absolute;
    width: 100%;
    height: 100%;
    span {
    display: block;
    font-size: 40vw;
    line-height: 1;
    color: rgba(255,255,255,.08);
    font-weight: 700;
    position: absolute;
    z-index: 5;
    right: 6%;
    top: -3.5%;
}
}