.footer{

 	background: url(../img/texture.png) center/cover no-repeat , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
    color: #fff;
    padding: 60px 0;
    position: relative;

     .cover-box {
        position: absolute;
        background: url(../img/texture.png) center/cover no-repeat , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
    }


 .center{

    max-width: 1200px;
    padding: 0px 15px;
    margin: 0px auto;
    }

    .flex-container {
        align-items: center;
        & > * {
            position: relative;
        }
    }
}


.footer_policy, .footer_logo {
    .flex();
}

.footer_logo {
    text-transform: uppercase;
    font-size: 20px;
}

.footer_policy {
    text-transform: uppercase;
    opacity: 0.4;
    p:first-child{
        margin-right: 20px;
    }
}

.footer_logo {
    p {
        margin-left: 25px;
    }
}

.footer_author {
    margin-right: 3.7%;
    p:first-child {
        font-family: "IBM";
    }

    p:nth-child(2) {
        text-transform: uppercase;
        font-family: "Trajan";
        color: @m_c;
        margin-top: 3px;
    }
}
   
  

   

   

