.gallery {

	position: relative;
	margin-top: -2px;
	margin-bottom: -3px;
	overflow-x: scroll;
	// overflow-y: hidden;
	background:  url(../img/texture.png) center no-repeat/cover , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
	
	

	& > * {
		position: relative;
		z-index: 3;
	}
	&:before {
		content: "";
		// padding-top: 21%;
		display: block;
		position: absolute;
		min-height: 210px;
		width: 100%;
		min-width: 1920px;
		left: 0;
		height: 100%;
		background: url(../img/gallery.png) no-repeat center/cover;
		z-index: 3;

		
	}

	&::-webkit-scrollbar-track {
    box-shadow: rgb(235, 229, 229) 0px 0px 6px inset;
    border-radius: 10px;
}

&::-webkit-scrollbar {
    height: 10px;
    width: 3px;
}

&::-webkit-scrollbar-thumb {
    background-color: #cb9831;
    width: 8px;
    height: 4px;
}


	&:after {
		content: "";
		// padding-top: 21%;
		display: block;
		min-height: 210px;
		height: 403px;
	}

	
}

.ball {
		position: absolute;
		top: 31%;
		right: -250px;
		width: 	694px;
		height: 	694px;
		background: #181817;
		border-radius: 50%;
		z-index: 1;
	}