.navigation{
	    overflow: hidden;
	    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
        z-index: 333;
    height: 95px;
    background-color: transparent;
    transition: all 0.45s ease-in-out 0s;
    .center, .flex-container {
    	height: 100%;
    }

    .center {
    	padding-right: 10px;
    	position: relative;
    }
}


.nav-group {
	.flex();
}


.social-item {
	display: flex;
	overflow: hidden;
	align-items: center;
	justify-content: center;
	width: 41px;
	height: 41px;
	background: @m_c;
	text-align: center;
	position: relative;
	& > * {
		position: relative;
		z-index: 1;
	}
	&:after {
		position: absolute;
		content: "";
		width: 100%;
		height: 100%;
		top: 0;
		left: -100%;
		background: #424242;
		z-index: 0;
		transition: all 0.45s ease-in-out;
	}
	&:hover:after {
		left: 0;
	}

	
	svg {
		width: 24px;
		height: 24px;
		fill: #fff;
	}
}


.nav-social {
	a {
	
			margin-right: 21px;
	
	}
}

.nav-phone, .nav-social {
	.flex();
}


.nav-group {
	li {
		padding: 13px 0;
		position: relative;
		&:hover:before {
			position: absolute;
		content: "";
		width: 105%;
		height: 3px;
		border: 2px solid @m_c;
		// border-radius: 5px;
		bottom:0;
		// left: -35px;
		z-index: 1;
		animation: bord 0.2s ease;
		}
	}
}


.is-selected {
	
	position: relative;
	z-index: 2;
	&> * {
		position: relative;
		z-index: 2;
	}
	&:before {
		position: absolute;
		content: "";
		width: 105%;
		height: 3px;
		// border-radius: 5px;
		border: 2px solid @m_c;
		bottom:0;
		// left: -35px;
		z-index: 1;
		animation: bord 0.2s ease;
	}
}


@keyframes bord {
	0% {
	 width:  0px;
	}

	100% {
	  width: 105%;
	}
}


.nav-wrap {
	font-weight: 900;
	li {
		letter-spacing: 1.05px;
	}
}


.nav-logo {
	width: 10%;
}


 .nav-phone {
	width: 28%;
	min-width: 491.67px;
}

 .nav-wrap {
 	width: 26.6%;
	min-width: 461.67px;
 }

.nav-group {
	
	max-width: 439px;

}

.nav-tel {
	font-weight: 900;
	letter-spacing: 1.05px;
	margin-right: 21px;
	transition: all 0.4s ease;
}

.nav-tel:hover {
	color: @m_c;
}
.nav-btn {
	transition: all 0s;
}