@import url('app/less/_fonts.less');
@import url('app/less/_variables.less');
@import url('app/less/_navigation.less');


html, body {
	overflow-x: hidden; 
	&::selection {
		color: @m_c;
		background: @m_c;
	
	}


}
body 
{
	margin: 0;
	padding: 0;	
	font-family: 'ProximaNova', sans-serif;
	font-size: 14px;
	font-weight: normal;
	color: #ffffff;
	&::selection {
		color: @m_c;

	}



}

h1, h2, h3, h4, h5, h6, p, div, span, li, blockquote {
	&::selection {
		
		background: @m_c;

	}
}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {

	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
	color: #ffffff;
