}

.ico-box {
    position: relative;
    width: 25px;
    height: 13px;

} 

.ico-anim {
	animation: rotate-b 0.5s linear forwards;
}

.show-menu-btn {
	display: none;
}

.show-menu-btn span {
    display: block;
    position: absolute;
    background: #ffffff;
    height: 1px;
    width: 20px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.15s ease-in-out;
    transition: 0.25s ease-in-out;
}

.show-menu-btn span:nth-child(1) {
    top: 0px;
}

.show-menu-btn span:nth-child(2) {
    top: 5px;
}

.show-menu-btn span:nth-child(3) {
    top: 10px;
}

.overlay-menu {
    top: 120px;
    position: fixed;
    background: #191919;
    min-height: 100%;
    overflow: auto;
    overflow: hidden;
    height: 100%;
    width: 0%;
    visibility: hidden;
    z-index: 9997;
    opacity: 0;
    padding: 50px 0;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.overlay-menu .texture {
	background: linear-gradient(to right top, #151515, #151515, #141414, #141414, #141313);
    top: 0;
    position: absolute;
   	height: 100%;
    right: 0px;
    width: 100%;
    img{
    	height: 100%;
    	width: 100%;
    }
 

}

.menu-table {
    display: table;
    width: 100%;
    height: 100%;
}

.menu-table-cell {
    display: table-cell;
    vertical-align: middle;
    position: relative;
    top: -100px;
    text-align: center;
    .overlay-nav {
    position: relative;
    list-style: none;
    padding: 0;
    z-index: 20;
    li {
    margin: 0;
    left: -60px;
    -webkit-transition: left 0.4s ease-out 0.4s;
    transition: left 0.4s ease-out 0.4s;
    opacity: 0;
    position: relative;
    a {
    font-weight: 700;
    font-size: 26px;
    line-height: 50px;
    display: inline-block;
    position: relative;
    letter-spacing: 2px;
    overflow: hidden;
    text-decoration: none;
    color: #ffffff;
    text-transform: capitalize;
    -webkit-transition: 0.15s ease-in-out;
    transition: 0.15s ease-in-out;
    margin-bottom: 5px;
}
}
}
}

.active-menu {
    width: 100%;
    visibility: visible;
    opacity: 1;
}

.menu-opened {
	position: fixed;
	width: 	100%;
}

.anim-burger {

	span:nth-child(1) {
		transform: rotate(90deg);
		top: 7px;
		will-change: transform;
	}

	span:nth-child(2) {
		opacity: 0;
		will-change: opacity;
	}

	span:nth-child(3) {
		transform: rotate(0deg);
		top: 6.5px;
		will-change: transform;
	}
}

@keyframes rotate-b {
	0% {
		transform: rotate(0);
	}  

	100% {
		transform: rotate(45deg);
	}
}


.overlay-menu.active-menu .overlay-nav li {
    left: 0;
    -webkit-transition: left 0.4s ease-out 0.3s, opacity 0.4s ease-out;
    transition: left 0.4s ease-out 0.3s, opacity 0.4s ease-out;
    opacity: 1;
}

.overlay-menu.active-menu .overlay-nav li:first-child {
    -webkit-transition-delay: .3s;
    transition-delay: .3s;
}

.overlay-menu.active-menu .overlay-nav li:nth-child(2) {
    -webkit-transition-delay: .34s;
    transition-delay: .34s;
}

.overlay-menu.active-menu .overlay-nav li:nth-child(3) {
    -webkit-transition-delay: .38s;
    transition-delay: .38s;
}

.overlay-menu.active-menu .overlay-nav li:nth-child(4) {
    -webkit-transition-delay: .42s;
    transition-delay: .42s;
}

