.mobile-send-button{
  margin-left: auto;
      display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.16), 0 2px 4px 0 rgba(0,0,0,.12);
    -webkit-box-shadow: 0 1px 3px 0 rgba(0,0,0,.16), 0 2px 4px 0 rgba(0,0,0,.12);
    -moz-box-shadow: 0 1px 3px 0 rgba(0,0,0,.16),0 2px 4px 0 rgba(0,0,0,.12);
    justify-content: center;
    -moz-justify-content: center;
    -webki-justify-content: center;
    -webkit-box-align: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    -moz-align-items: center;
    position: relative;
    width: 45px;
    height: 45px;
    padding: 0;
    -webkit-transition-duration: 400ms;
    transition-duration: 400ms;
    transition-property: all;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-transition-property: all;
    transition-timing-function: cubic-bezier(.7,1,.7,1);
    -webkit-transition-timing-function: cubic-bezier(.7,1,.7,1);
    z-index: 8;
        background-color: #fff;
}
