.formats_card:first-child {
	border-top: 0;
}

.formats_card:last-child {
	margin-bottom: 0;
}

.formats_card-title, .h3_title {
	text-align: center;
	font-size: 2.5vw;
	// flex: 0 1 50%;
	width: 	100%;
}

.formats_card-title {
	text-align: left;
	margin-left: 10px;
}

.formats_card-text {
	font-size: 1.5vw;
	padding: 25px 15px 20px;
	overflow-y: auto;
	max-height: 65%; 
	position:relative;
	padding-left: 40px;
	line-height: 1.65;
	
}

.formats_card-text::-webkit-scrollbar-thumb {
		background-color: @m_c;
		width: 8px;
		height: 4px;

	}

	.formats_card-text::-webkit-scrollbar {
    width: 3px;
}
 
.formats_card-text::-webkit-scrollbar-track {
	 -webkit-box-shadow: inset 0 0 6px #ebe5e5; 
    
    border-radius: 10px;
}

.formats_card-img {
	flex: 0 1 40%;
	position: relative;
}


.formats_card-img:after {
	content: "";
	width: 	100%;
	height: 100%;
	top: 0;
	left: 0;
   position: absolute;
    background: linear-gradient(to right,rgba(#212121, .99),rgba(21,21,21,0));
}

.formats_card-img {
	position: relative;
	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
	}
	&:before {
		content: "";
		display: block;
		padding-top: 55%;
		
	}
}
.formats_card-innerWrap {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 	100%;

}

