.mission {
	background:  url(../img/texture.png) center no-repeat/cover ,  linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
	color: #fff;

	.center {
		max-width: 830px;
    padding: 0px 15px;
    margin: 0px auto;
    position: relative;
	}

	.cover-box {
    	position: absolute;
    }

    .to-bottom a:after {
    	content: none;
    }


}

.mission {
	.h3_mission-title {
		width: 150%;
	}
	.to-bottom {
		margin-top: 11%;
		a {
			bottom: 0;
		}
	}
}


.misiion_three-kit, .mission_triangle {
		flex: 0 1 50%;
}

.mission_triangle {
	position: relative;
	&:before {
		content: "";
		display: block;
		padding-top: 135%;
	}
	.mission_triangle-threeangle, .mission_triangle-golden {
		position: absolute;
    width: 150%;
    max-width: 510px;
    max-height: 510px;
    height: 100%;
    top: 37%;
    left: -35%;

	}

	.mission_triangle-golden {
		top: 0;
    left: 0;
	}

	}


.rotate-img {

		width: 	100%;
		height: 	100%;
		transform-origin: 41.5% 41%;
		animation: rot linear infinite 30s;

}


	


	.mission_triangle-golden  {
		
		img {
			
			width: 	78%;
			position: absolute;
			top: 2%;
			left: 0;
			
		}
	}



@keyframes rot {
	0%{
		transform: rotate(0deg);
	} 

	100% {
		transform: rotate(-360deg);
	}
}





.mission_content {
	max-height: 405px;
	overflow-y: scroll;
	position: relative;
	margin-top: 47px;


	&::-webkit-scrollbar-thumb {
		background-color: @m_c;
		width: 8px;
		height: 4px;

	}

	&::-webkit-scrollbar {
    width: 3px;
}
 
&::-webkit-scrollbar-track {
	 -webkit-box-shadow: inset 0 0 6px #ebe5e5; 
    		
    border-radius: 10px;
}
	// transform: perspective(1000px);
	// transform-style: preserve-3d;
	// transform-origin: 50% 100px 50% ;
	// backface-visibility: hidden;
	//
	// animation: rot-text linear infinite 60s;

	
	// .shadow-holder {
	// 	position: absolute;
	// 	top: 0;
	// 	left: 0;
	// 	width: 	100%;
	// 	height: 	100%;
	// 	box-shadow: inset 10px 100px 75px rgba(0,0,0,0.8); 
	// 	background:  url(../img/texture.png) center no-repeat/cover , linear-gradient(to bottom, rgba(0,0,0,0.95), rgba(0,0,0,.5));


	// }
	&:before, &:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 10%;
		background:  url(../img/texture.png) center no-repeat/cover , linear-gradient(to bottom, rgba(#0b0b0b,0.99), rgba(#070707,0.99));
		z-index: -1;
		
	}

	 &:after {
	 	top: 90%;
	 	background:  url(../img/texture.png) center no-repeat/cover , linear-gradient(to top, rgba(#0b0b0b, 0.99), rgba(#070707 ,0.99));
	 }
}


@keyframes rot-text {
	0% {
		transform: rotateX(0);
	}

	100% {
		transform: rotateX(360deg);
	}
}


.mission-kit {
	color: #fff;
	font-size: 1.5vw;
	margin-bottom: 40px;
	
}

.h3_missionKit-title, .h3_mission-title, .h3_missionPrecious-title{
	font-size: 3.75vw;
}

.missionKit_descr {
	letter-spacing: 1.08px;
	line-height: 1.55;
}

.misiion_three-kit {
	position: relative;
	&:before {
		display: block;
		padding-top: 100%;
		content: '';

	}

}

.h3_missionKit-title {
	margin-bottom: 27px;
}

.h3_mission-title, .mission_content {
	position: absolute;
	width: 150%;
	top: 0;
	left: 0;
}

.mission .h3_mission-title,
.mission .h3_missionKit-title {
	top: -45px;
	text-align: left;
}


.texture-box {

		width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    overflow: hidden;
    position: absolute;
  background:  url(../img/bg-ground.png) center repeat-y/cover;
    background-size: cover; /* Меняем масштаб */
	
}

.mission-picture {
    z-index: 15;
    position: absolute;
    width: 310px;
    height: 370px;
    float: right;
    top: -275px;
    margin-top: -100px;
    left: 88%;
    .mission-picture-box {
    	position: relative;
    width: 270px;
    height: 370px;
    border: 10px solid #ffffff;
    box-shadow: 0px 45px 45px rgba(0, 0, 0, 0.23);
    // overflow: hidden;
    .cover-box {
		overflow: hidden;
    position: relative;
        width: 100%;
    height: 100%;
    img {
    	position: absolute;
    width: 250px;
    height: auto;
    top: -1px;
    left: 0px;
    }
    }
}
}







