.cases{

 	background: url(../img/texture.png) center/cover no-repeat , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
    color: #fff;

    .main-btn {
    	max-width: 344px;
    	margin: 0 auto;
    	margin-top: 6%;
        width:  100;
    }
   
  

    .cover-box {
    	position: absolute;
    	background: url(../img/texture.png) center/cover no-repeat , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
    }

    .center{

	max-width: 1200px;
    padding: 0px 15px;
    margin: 0px auto;
    }

    .flex-container {
        position: relative;
    }

}


.cases-title {
    margin-top: 25%;
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 67px;

}

.cases-slider {
    opacity: 0.99;
    width:  100%;
    position: relative;
    z-index: 10;

    .slick-slide {
       & > div {
        display: flex;
        justify-content: center;
       }
    }
}

.cases-slide {
    max-width: 336px;
    margin: 0 36px;
   
}

.cases-slider .slick-list {
    overflow-x: hidden;
    padding: 30px 0;
    // margin: 0 -36px;
}

.cases_slide-head {
    display: flex;  
    justify-content: center;
    align-items: center;
    width:  100%;
    img {
        width: 118px;
        height: 118px;
    }
}


.cases-slide_before {
    position: absolute;
     max-width: 336px;
    width:  100%;
    height: 100%;
    top: 35px;
    // left: -15px;
    background: @m_c;
    z-index: -2;
}

.cases-slide-innerWrap {
    width: 100%;
    height: 100%;
     .flex();
    flex-direction: column;
    display: flex !important;
    border: 8px solid #ffffff;
    max-width: 336px;
    background: #212121;
   
    position: relative;
    z-index: 25;
    
    padding-bottom: 35px;
}

.cases_slide-niche {
    text-align: center;
    font-size: 20px;
    font-weight: 800;
}

.cases_slide-direction {
    font-size: 18px;
    opacity: 0.8;
    margin-top: 5px;
}

.cases_slide-title {
    text-align: left;
    margin-left: 10px;
    line-height: 1.3;
}

.case_slide-text {
	.flex();
	margin-top: 25px;
	width: 100%;
	justify-content: space-around;
}

.case_slide-desc {
	margin-top: 15px;
}

.cases_points-holder {
	position: relative;
	&:before {
		position: absolute;
		content: "";
		width: 2px;
		top: 10%;
		height: 80%;
		background: @m_c;
		left: 23px;
	}
	margin-right: 10%;
	height: 75px;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	span {
		display: inline-block;
		margin-right: 25px;
		position: relative;
        font-weight: 400;
		&:before {
			position: absolute;
			top: 30%;
			left: 20px;
			content: "";
			width: 7px;
			height: 7px;
			border-radius: 50%;
			background: @m_c;
		}
	}
}

.cases_slide-term {
	font-weight: 800;
}

.poin_a, .point_b {
    font-weight: 800;
}