.myPopup {
	max-width: 530px;
	margin: 0 auto;
	width: 100%;

	.center {
		background: #212121;
		background-size: cover;
	
		padding: 13.96% 16.41% 12.64% 16.41%;
		text-align: center;
		border: 8px solid #ffffff;
		position: relative;
		.before-popup {
			position: absolute;
			top: 15px;
			left: -35px;
			width: 	100%;
			height: 	105%;
			background: @m_c;
			z-index: -1;
		}
	}

	.popup_form {
		.footer_btn {
			margin-top: 12px;
		}
		.flex();
		flex-direction: column;
		justify-content: center;
		width: 100%;
		.text-field {
			border: 1px solid #515151;
			max-width: 369px;
			color: @font_color;
			&::-webkit-input-placeholder {
    	color: @font_color;
    }

     &::-moz-input-placeholder {
    	color: @font_color;
    }
		}
	}

	
}


.popup_title {
	margin-bottom: 35px;
	&:before {
		content: none;
	}
}


.popup_description {
	margin-bottom: 30px;
	font-size: 16px;
}

.drinks_popup {
	.popup_img-layer {
		margin: 0 auto;
		width: 100%;
		height: 100%;
		background: #000;
		position: relative;
		&:before {
			content: "";
			display: block;
			padding-top: 100%;
		}
	}

	.popup_text {
		margin-top: 20px;
		text-align: left;
		line-height: 1.35;
	}
}

.thanks_popup.myPopup {
	.center {
		padding: 13.96% 11.49% 12.64%;
	}

	.h2_title {
		word-spacing: -0.2px;
	}
	
	max-width: 700px;
	.footer_btn {
		margin: 	0 auto;
		max-width: 105px;
	}

	.popup_description {
		margin-bottom: 	27px;
		font-size: 21px;
	}

	.popup_answer {
		
		font-size: 21px;
		max-width: 420px;
		margin: 	0 auto;
		margin-bottom: 	30px;
	}

}


.mfp-close-btn-in .mfp-close:after, .mfp-close-btn-in .mfp-close:before {
	background: #ffffff;
}


.my-mfp-zoom-in {
	.mfp-container {
		padding: 0 40px;
		padding-right: 10px;
	}
}