.made {

    background: url(../img/texture.png) center no-repeat/cover , linear-gradient(to right top, #070707, #080808, #090909, #0a0a0a, #0b0b0b);
   
    color: #fff;
    padding-top: 40px;

    .cover-box {
    	position: absolute;
    }

    .center {

		max-width: 1200px;
    padding: 0px 15px;
    margin: 0px auto;
    }

    .flex-container {
    	padding-left: 55px;
    }
	

}



.made-title {
	max-width: 800px;
	margin: 0 auto;
	margin-bottom: 75px;
}

.benefits__number {
	width: 	100%;
	

	
	font-size: 97px;
	font-weight: 800;
	small {
		font-size: 40px;
	}
}


.benefits__number-wrap {
	.flex();
	align-items: flex-start;
}

.benefits__number-2 {
	font-size: 34px;
	font-weight: 800;
	margin-top: 13px;

}


.benefits__element {
	width: 	100%;
	max-width: 178.78px;
	position: relative;
	.flex();
	flex-direction: column;
	justify-content: center;
	align-items: center;

	& > * {
		position: relative;
		z-index: 10;
	}
	
}

.benefits__number-wrap {
	position: relative;
	z-index: 2;
	width: 100%;
}

.benefits__number-before {
		position: absolute;
		width: 	300%;
		max-width: 214px;
		height:  12px;
		background: @m_c;
		bottom: 25px;
		left: -23%;
		transform: translateX(0%);
		z-index: -1;
}

.benefits__number-after {
		position: absolute;
		width: 	100%;
		max-width: 40px;
		height:  2px;
		background: @m_c;
		bottom: 35px;
		left: 95%;
		transform: translateX(0%);
		z-index: -1;
}

.benefits__number {
	text-align: center;		
	position: relative;
	z-index: 2;
