}


.benefits__element:last-child .benefits__number-before {
left: -23%;
}

.benefits__title {
	text-transform: uppercase;
}


.animated {
	animation: slide-left 3500ms linear;
}

.animated.changeOpacity {
	animation: opacc 3500ms linear;
}

// .animated:after {
// 	animation: opacc 6s linear;
// }

@keyframes slide-left {
	0% {
		width: 0;
	}

	100% {
		width: 150%;
	}
}


@keyframes opacc {
	0% {
		opacity: 0;
	}

	99% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

